import { useEffect, useRef, useState, useCallback } from "react";

type UseCounterOptions = {
  paused?: boolean;
};

export default function useCounter(
  initialSeconds: number,
  options: UseCounterOptions = {}
) {
  const { paused = false } = options;
  const [counter, setCounter] = useState(initialSeconds);
  const [isFinished, setIsFinished] = useState(initialSeconds <= 0);
  const intervalRef = useRef<ReturnType<typeof setInterval> | null>(null);

  const start = useCallback(() => {
    if (intervalRef.current) return; // –µ—Å–ª–∏ —É–∂–µ –∑–∞–ø—É—â–µ–Ω ‚Äî –Ω–µ —Ç—Ä–æ–≥–∞–µ–º
    intervalRef.current = setInterval(() => {
      setCounter((prev) => {
        if (prev <= 1) {
          clearInterval(intervalRef.current!);
          intervalRef.current = null; // –≤–∞–∂–Ω–æ: —Å–±—Ä–æ—Å –ø–æ—Å–ª–µ clearInterval
          setIsFinished(true);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
  }, []);

  const reset = useCallback((sec: number) => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null; // –∏ –∑–¥–µ—Å—å —Ç–æ–∂–µ
    }
    setCounter(sec);
    setIsFinished(sec <= 0);
  }, []);

  useEffect(() => {
    if (!isFinished && counter > 0 && !paused) {
      start();
    } else if (paused && intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null; // –Ω–µ –∑–∞–±—ã–≤–∞–µ–º –æ–±–Ω—É–ª—è—Ç—å —Å—Å—ã–ª–∫—É
      }
    };
  }, [start, counter, isFinished, paused]);

  return { counter, isFinished, reset };
}
